import "../estilos/CSS-cotizacion.css";
// Importamos bootstrap
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min.js';
//Importamos jspdf
import jsPDF from 'jspdf';

const Cotizacion = () =>{
    
    function genPDF() {
        var doc = new jsPDF();
        let nombre = document.getElementById('nombre').value;
        let apellido = document.getElementById('apellido').value;
        let edad = document.getElementById('edad').value;
        let correo = document.getElementById('mail').value;
        let producto = document.getElementById('producto').value;
    
        doc.text('Asunto: Confirmacin de Cotizacin y Programacin de Cita.', 10, 10);
        doc.text(' ', 10, 20);
        doc.text('Estimado/a ' + nombre+',', 10, 30);
        doc.text(' ', 10, 40);
        doc.text('Es un placer para nosotros en "El rincn del mueble" recibir tu solicitud de', 10, 50);
        doc.text('cotizacin para el producto ' + producto, 10, 60);
        doc.text('Agradecemos tu inters en nuestros servicios y estamos emocionados', 10, 70);
        doc.text('por la oportunidad de trabajar con usted.', 10, 80);
        doc.text('A continuacin, le mencionamos un resumen de la informacin proporcionada:', 10, 90);
        doc.text(' ', 10, 100);
        doc.text('Nombre(s) del Cliente:' + nombre, 10, 110);
        doc.text('Apellido(s) del Cliente:  ' + apellido, 10, 120);
        doc.text('Edad del Cliente: ' + edad, 10, 130);
        doc.text('Correo Electrnico: ' + correo, 10, 140);
        doc.text('Producto Solicitado: ' + producto, 10, 150);
        doc.text(' ', 10, 160);
        doc.text('Le hemos programado una cita para la siguiente fecha: ', 10, 170);
        doc.text('Fecha: Lunes, 18 de diciembre de 2023. ', 10, 180);
        doc.text('Hora: 10:00 a.m. ', 10, 190);
        doc.text(' ', 10, 200);
        doc.text('Usted pude cambiar la fecha y el dia de la reunion llamando al 78764903. ', 10, 210);
        doc.text(' ', 10, 220);
        doc.text('Agradecemos nuevamente tu inters en "El rincn del mueble" y  ', 10, 230);
        doc.text('esperamos verte pronto!', 10, 240);
    
        doc.save('CotizacionProductoTs.pdf');
        
        alert('No olvide llevar el comprobante de cotizacin.')
    }
    return (
        <body class="formo">
        <div className="container" id="formulario">
        {/* Creamos un formulario */}
        <div className="cont col-sm-12 col-md-8 col-lg-8 col-xl-8 bg-white py-5 px-5">
            <h2 className="centrado">Haz tu cotizacin personalizada</h2>
            <form>
                {/* Aqu se ingresan los datos personales */}
                <div className="mb-3">
                    <label htmlFor="nombre" className="form-label">
                        Nombre
                    </label>
                    <input
                        type="text"
                        className="form-control"
                        id="nombre"
                        placeholder="Ingrese su nombre(s)"
                    />
                </div>
                <div className="mb-3">
                    <label htmlFor="apellido" className="form-label">
                        Apellido
                    </label>
                    <input
                        type="text"
                        className="form-control"
                        id="apellido"
                        placeholder="Ingrese su(s) apellidos(s)"
                    />
                </div>
                {/* Aqu se ingresa la edad */}
                <div className="mb-3">
                    <label htmlFor="edad" className="form-label">
                        Edad
                    </label>
                    <input type="number" className="form-control" id="edad" />
                </div>
                {/* Aqu se ingresa el email */}
                <div className="mb-3">
                    <label htmlFor="mail" className="form-label">
                        Direccin de correo
                    </label>
                    <input type="email" className="form-control" id="mail" aria-describedby="emailHelp" placeholder="Ingrese su correo" />
                    <div id="advertenciaMail" className="form-text">
                        Tu cuenta no se compartir con nadie.
                    </div>
                </div>
                {/* Aqu se dejan los comentarios */}
                <div className="mb-3">
                    <label htmlFor="producto" className="form-label">
                        Producto(s)
                    </label>
                    <textarea
                        className="form-control"
                        id="producto"
                        rows="3"
                    ></textarea>
                </div>
                {/* Aqu se verifican los datos */}
                <div className="mb-3 form-check">
                    <input
                        type="checkbox"
                        className="form-check-input"
                        id="exampleCheck1"
                    />
                    <label className="form-check-label" htmlFor="checkbox">
                        Todo listo?
                    </label>
                </div>
                <div className="d-grid gap-2">
                    {/* Esto es para que los botones ocupen todo el ancho */}
                    <button type="button" className="btn btn-primary" onClick={genPDF}>
                        Generar documento PDF
                    </button>
                </div>
            </form>
        </div>
    </div>
    </body>
    )
}
export default Cotizacion;