import { Outlet, Link } from "react-router-dom"
// Importamos css y img para el header
import "../estilos/CSS-layout.css";
import logoblanco from "../imagenes/logoblanco.png";
import menu from "../imagenes/menu.svg";
// Importamos css y img para el footer
import "../estilos/CSS-footer.css";
import React, { useEffect } from 'react';

const Layout = () =>{
    //iconos
    useEffect(() => {
        const script = document.createElement('script');
        script.src = 'https://kit.fontawesome.com/6da47a7b84.js';
        script.crossOrigin = 'anonymous';
        document.body.appendChild(script);
        return () => {
        document.body.removeChild(script);
        };
    }, []);

    return (
    <div>
        <header className="header-nav">

        <div className="logo-nav">
            <img src={logoblanco} alt="" width="70px" />
            <h1 className="ltr" id="tit-nav">El rincn del mueble</h1>
        </div>

        <input type="checkbox" id="toggle-nav" />

        <label htmlFor="toggle-nav">
            <img className="menu-nav" src={menu} alt="menu-nav" />
        </label>

        <nav className="navigation-nav">
            <ul>
            <li>
                    <Link to="/">Inicio</Link>
            </li>

            <li>
                    <Link to="/producto">Productos</Link>
            </li>
            <li>
                    <Link to="/proceso">Proceso</Link>
            </li>
            <li>
                    <Link to="/blog">Blog</Link>
            </li>
            <li>
                    <Link to="/cotizacion">Cotizacion</Link>
            </li>
            <li>
                    <Link to="/faqs">Faqs</Link>
            </li>
            <li>
                    <Link to="/creador">Creador</Link>
            </li>
            </ul>
        </nav>
        </header>
        <div className="relleno">
        <br/><br/><br/><br/><br/><br/>
        </div>
        {/* contenido dinamico */}
        <Outlet/>
        {/* footer */}
        <footer>
            <footer className="pie-pagina">
                <div className="grupo-1">
                    <div className="box">
                    <figure>
                        <a href="#">
                        <img src={logoblanco} alt="Logo" />
                        </a>
                    </figure>
                    </div>
                    <div className="box">
                    <h2>SOBRE NOSOTROS</h2>
                    <p>
                        En El Rincn del Mueble, fusionamos calidad y estilo para
                        transformar tus espacios. Descubre nuestra pasin por el diseo que
                        marca la diferencia en cada rincn de tu hogar.
                    </p>
                    </div>
                    <div className="box">
                    <h2>SIGUENOS</h2>
                    <div className="red-social">
                        <a
                        href="https://www.facebook.com/csar.mchicado"
                        className="fa fa-facebook"
                        ></a>
                        <a
                        href="www.instagram.com/cesar_m_787"
                        className="fa fa-instagram"
                        ></a>
                        <a href="https://twitter.com/CesaR_8718" className="fa fa-x"></a>
                        <a href="https://github.com/CesarRDJ" className="fa fa-github"></a>
                    </div>
                    </div>
                </div>
                <div className="grupo-2">
                    <small>
                    &copy; 2023 <b>El rincn del mueble</b> - Todos los Derechos
                    Reservados.
                    </small>
                </div>
            </footer>
        </footer>
    </div>
    );
};
export default Layout;